<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <mt-button type="default" @click="clickH">default</mt-button>
    <!-- <mt-button type="primary" @click="clickP">primary</mt-button> -->
    <!-- <mt-button type="danger">danger</mt-button>
          <mt-button plain>plain</mt-button>
          <mt-button icon="back">back</mt-button>
          <mt-button icon="more">更多</mt-button> -->
    <mt-button>
      <img src="../src/assets/1.png" height="20" width="20" slot="icon"> 自定义图标
    </mt-button>

    <i class="fa fa-bath my-font"></i>
    <mt-button @click="loading" type="primary">加载更多</mt-button>
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
  methods: {
    clickH() {
      this.$toast({
        message: '虫子',
        position: 'top',
        iconClass: 'fa fa-bicycle my-font',
        // duration:100000,
      })
    },
    loading() {
      this.$indicator.open('loading.......')
      setTimeout(()=>{
        this.$indicator.close()
      }, 5000);

    }

  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.my-font {
  font-size: 15rem;
  color: darkcyan;
}

.mint-toast-text {
  color: darkorange;
  font-size: 3rem;
}
</style>



